// It doesn't make sense to have this ETHER definition here, this file should be for tokenlist URIs only.
export const ETHER = networkConfig.nativeAsset;

type TokenListConfig = {
  HEDG: {
    Default: string;
    Vetted: string;
  };
  External: string[];
};

